let db = openDatabase('airport','1.0','test db',1024*1024*100); 

export function create_table() {
    const sql =
        'CREATE TABLE IF NOT EXISTS airport' +
        '(id INTEGER PRIMARY KEY autoincrement,' +
        'type int(10) NOT NULL,' +
        'x FLOAT NOT NULL,' +
        'y FLOAT NOT NULL,' +
        'desc varchar(200) NOT NULL,' +
        'time int NOT NULL);'
    db.transaction((tx) => {
        tx.executeSql(sql)
    })
}

export function save_record(record, callback) {
    const sql =
        'INSERT INTO airport (type, x, y, desc, time) VALUES (?, ?, ?, ?, ?)'
    db.transaction((tx) => {
        tx.executeSql(sql, [
            record.type,
            record.x,
            record.y,
            record.desc,
            record.time,
        ], (tx, rs) => {
            callback && callback()
        }, (tx, err) => {
            console.log(tx, err)
        })
    })
}

export function update_record(record, callback) {
    const sql =
        'UPDATE airport SET' +
        ' type=' + record.type +
        ',desc=' + '"' + record.desc + '"' + 
        ',time=' + record.time +
        ' WHERE id=' + record.id
    db.transaction((tx) => {
        tx.executeSql(sql, [], (tx, rs) => {
            callback && callback()
        }, (tx, err) => {
            console.log(tx, err)
        })
    })
}

export function delete_record(recordId, callback) {
    const sql =
        'DELETE FROM airport WHERE id=' + recordId
    db.transaction((tx) => {
        tx.executeSql(sql, [], (tx, rs) => {
            callback && callback()
        }, (tx, err) => {
            console.log(tx, err)
        })
    })
}

export function get_all_records(callback) {
    const sql =
        'SELECT * FROM airport'
    db.transaction((tx) => {
        tx.executeSql(sql, [], (tx, rs) => {
            callback && callback(tx, rs)
        })
    })
}

export function clear_data() {
    const sql = 'DELETE FROM airport'
    db.transaction((tx) => {
        tx.executeSql(sql)
    })
}