<template>
  <el-popover
    placement="top-start"
    width="450"
    @after-leave='refreshRecords'
    v-model="visible"
    trigger="manual">
		<div v-if='mode === "check"'>
			<h3>{{parseTime(record.time)}}</h3>
            <div class="type-container" style="margin-bottom: 10px">
                <el-button type="primary" v-if='record.type === 0' size="mini" @click="changeType(0)">类型一</el-button>
                <el-button type="success" v-if='record.type === 1' size="mini" @click="changeType(1)">类型二</el-button>
                <el-button type="info" v-if='record.type === 2' size="mini" @click="changeType(2)">类型三</el-button>
                <el-button type="warning" v-if='record.type === 3' size="mini" @click="changeType(3)">类型四</el-button>
                <el-button type="danger" v-if='record.type === 4' size="mini" @click="changeType(4)">类型五</el-button>
            </div>
            <div>{{record.desc}}</div>
		</div>
        <div v-else-if="mode === 'add' || mode === 'edit'">
            <!-- <h3 v-if="mode === 'add'">{{parseTime(record.time)}}</h3> -->
            <el-date-picker
                v-model="record.time"
                type="datetime"
                placeholder="选择日期时间"
                align="right"
                size="mini"
                format="yyyy年MM月dd日 hh:mm:ss"
                value-format='timestamp'
                :picker-options="pickerOptions"
                style="margin-bottom: 10px">
            </el-date-picker>
            <div class="type-container" style="margin-bottom: 10px">
                <el-button type="primary" :plain='record.type !== 0' size="mini" @click="changeType(0)">类型一</el-button>
                <el-button type="success" :plain='record.type !== 1' size="mini" @click="changeType(1)">类型二</el-button>
                <el-button type="info" :plain='record.type !== 2' size="mini" @click="changeType(2)">类型三</el-button>
                <el-button type="warning" :plain='record.type !== 3' size="mini" @click="changeType(3)">类型四</el-button>
                <el-button type="danger" :plain='record.type !== 4' size="mini" @click="changeType(4)">类型五</el-button>
            </div>
            <div style="margin-bottom: 10px">
                <el-input
                    type="textarea"
                    :rows="2"
                    placeholder="请输入内容"
                    v-model="record.desc">
                </el-input>
            </div>
            <div>
                <el-button type="primary" size='mini' @click="confirmReport">确定</el-button>
                <el-button size='mini' @click="cancelReport" v-if="mode === 'add'">取消</el-button>
                <el-button size='mini' @click="deleteReport" type="danger" v-else-if="mode === 'edit'">删除</el-button>
            </div>
        </div>
    <div
      slot="reference"
      class="record-container"
      @click="clickRecord"
      @mouseenter="mouseenter"
      @mouseleave="mouseleave"
      :style="{
          top: record.y * imgWidth - 5 + 'px',
          left: record.x * imgWidth - 5 + 'px',
          background: tag[record.type].color
      }">
    </div>
  </el-popover>
  
</template>

<script>
  import { save_record, delete_record, update_record } from '../db/actions'
  import moment from 'moment'
  export default {
    data () {
      return {
          visible: false,
          tag: [
              {
                tag: '',
                color: '#4EA5FF',
                text: '类型一'
              },
              {
                tag: 'success',
                color: '#73C74B',
                text: '类型二'
              },
              {
                tag: 'info',
                color: '#E0E0E1',
                text: '类型三'
              },{
                tag: 'warning',
                color: '#E5A04C',
                text: '类型四'
              },
              {
                tag: 'danger',
                color: '#F46E6F',
                text: '类型五'
              }
          ],
          pickerOptions: {
            shortcuts: [{
                text: '今天',
                onClick(picker) {
                picker.$emit('pick', new Date());
                }
            }, {
                text: '昨天',
                onClick(picker) {
                const date = new Date();
                date.setTime(date.getTime() - 3600 * 1000 * 24);
                picker.$emit('pick', date);
                }
            }, {
                text: '一周前',
                onClick(picker) {
                const date = new Date();
                date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                picker.$emit('pick', date);
                }
            }]
            },
      }
    },
    props: {
        record: {
            type: Object,
            default: null
        },
        imgWidth: {
            type: Number,
            default: 0
        },
        mode: {
            type: String,
            default: 'check'
        }
    },
    mounted() {
        if (this.mode === 'add') {
            this.visible = true
        }
    },
    watch: {
        mode() {
            this.visible = false
        }
    },
    methods: {
        parseTime(timstamp) {
            return moment.unix(timstamp / 1000).format('YYYY年MM月DD日 hh:mm:ss')
        },
        cancelReport() {
            if (this.mode === 'add') {
                this.$emit('cancel-add-record')
                this.visible = false
            } else if (this.mode === 'edit') {
                this.$emit('cancel-edit-record')
            }
        },
        changeType(type) {
            this.record.type = type
        },
        confirmReport() {
            if (this.mode === 'add') {
                save_record(this.record, () => {
                    this.$message.success('创建成功')
                    this.$emit('add-record-success')
                    this.visible = false
                })
            } else if (this.mode === 'edit') {
                update_record(this.record, () => {
                    this.$message.success('修改成功')
                    this.visible = false
                    this.$emit('refresh-records')
                })
            }
        },
        refreshRecords () {
            if (this.mode !== 'add') {
                this.$emit('refresh-records')
            }
        },
        deleteReport () {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                delete_record(this.record.id, () => {
                    this.$emit('refresh-records')
                    this.visible = false
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        },
        mouseenter() {
            if (this.mode === 'check') {
                 this.visible = true
            }
        },
        mouseleave() {
            if (this.mode === 'check') {
                 this.visible = false
            }
        },
        clickRecord() {
            if (this.mode === 'edit') {
                 this.visible = !this.visible
            }
        }
    }
  }
</script>

<style lang='less'>
.record-container{
    width: 10px;
    height: 10px;
    border-radius: 10px;
    position: absolute;
    box-sizing: content-box;
    &:hover{
      border: 2px solid #fff;
    }
    .type-container{
        margin-bottom: 10px;
    }
}
</style>
