<template>
  <div class="main">
      <h1>机场系统</h1>
      <div class="actions">
        <el-tabs v-model="mode" type="card" @tab-click="handleClick">
          <el-tab-pane label="查看" name="check"></el-tab-pane>
          <el-tab-pane label="编辑" name="edit"></el-tab-pane>
          <el-tab-pane label="添加" name="add"></el-tab-pane>
        </el-tabs>
      </div>
      <div id='airport-image-wrapper' class="image-wrapper" @click='getPos'>
        <img src="../assets/airport.jpg" alt="">
        <Record
            :imgWidth='imgWidth'
            v-for="(record, index) in records"
            :key='index'
            :record='record'
            :mode='mode'
            @cancel-add-record='cancelAdd'
            @refresh-records='fetchRecords'
            @add-record-success='addRecordSuccess'/>
      </div>
  </div>
</template>

<script>
  import { getElementPos, getMousePos } from '../utils/dom'
  import { save_record, get_all_records, clear_data } from '../db/actions'
  import Record from './Record'
  import moment from 'moment'
  export default {
    data () {
      return {
          mode: 'check',
          imgWidth: 0,
          records: []
      }
    },
    components: {
        Record
    },
    created (){
        // clear_data()
        this.fetchRecords()
    },
    mounted () {
        this.imgWidth = document.getElementById('airport-image-wrapper').clientWidth
    },
    methods: {
        getPos (e) {
            if (this.mode != "add") return;
            let point = getMousePos(e)
            let target = document.getElementById('airport-image-wrapper')
            var imgWrapper = {
                x: getElementPos('airport-image-wrapper').x,
                y: getElementPos('airport-image-wrapper').y,
                width: target.clientWidth
            }
            if (!this.records.length) {
                this.records = [{
                x: (point.x - imgWrapper.x) / this.imgWidth,
                y: (point.y - imgWrapper.y) / this.imgWidth,
                type: 0,
                time: moment().unix() * 1000,
                desc: ''
            }]
            }
        },
        cancelAdd () {
            this.records = []
        },
        refreshRecords() {
            this.fetchRecords()
        },
        handleClick(tab, event) {
          if (this.mode == 'add') { 
              this.records = []
          } else {
              this.fetchRecords()
          }
        },
        addRecordSuccess () {
            this.records = []
        },
        fetchRecords() {
            get_all_records((tx, rs) => {
                this.records = []
                for (let i = 0; i < rs.rows.length; i++) {
                    this.records.push(rs.rows[i])
                }
            })
        }
    }
  }
</script>

<style lang='less'>
.image-wrapper{
  width: 100%;
  height: auto;
  border: 1px solid #333;
  position: relative;
  cursor: pointer;
  img {
    width: 100%;
  }
}
</style>
